import {
	appGetHotels,
	appGetMS,
	getHotelInfoByHotelName
} from '../../../api/hotels.js'
export default {
	data() {
		return {
			params: {},
			data: [],
			pageNum: 1, //当前获取数据的页数
			pageSize: 10, //每页多少条
			type: null,
			countNight: null,
			inputVal: null
		}
	},
	onLoad(data) {
		if (data.name) {
			this.inputVal = data.name
			let params = {
				ids: data.name
			}
			getHotelInfoByHotelName(params).then(data => {
				this.data = data.data;
			}).catch(err => {})
		} else {
			this.countNight = data.nightNum;
			this.type = data.type
			let params = {
				area: data.area,
				price: data.lowPrice,
				type: data.star,
				time: data.date,
				pageNum: this.pageNum,
				pageSize: this.pageSize
			}
			for (let key in params) {
				if (params[key] == 'undefined') {
					delete params[key]
				}
			}
			this.params = params;
			if (this.type === '小时房') {
				let time = data.date.split(',')[0];
				this.params.time = time
			}
			switch (this.type) {
				case "邯郸":
					this.appGetHotels(params)
					break;
				case "小时房":
					this.appGetHotels(params)
					break;
				case "民宿公寓":
					this.appGetMS(params)
					break;
				default:
					this.search = data.name
					break;
			}
		}
	},
	//触底刷新
	onReachBottom() {
		// 一次10条
		this.pageNum++;
		this.params.pageNum++;
		switch (this.type) {
			case "邯郸":
				this.appGetHotels(this.params)
				break;
			case "小时房":
				this.appGetHotels(this.params)
				break;
			case "民宿公寓":
				this.appGetMS(this.params)
				break;
			default:
				this.appGetHotels(this.params)
				break;
		}
	},
	// 监听input的值和按钮操作事件和软键盘上的搜索按钮
	onNavigationBarButtonTap(val) {
		// 执行某些操作
		let params = {
			ids: this.inputVal
		}
		getHotelInfoByHotelName(params).then(data => {
			this.data = data.data;
		}).catch(err => {})
	},
	onNavigationBarSearchInputChanged(val) {
		this.inputVal = val.text;
	},
	onNavigationBarSearchInputConfirmed(val) {
		// 同button的事件
		let params = {
			ids: this.inputVal
		}
		getHotelInfoByHotelName(params).then(data => {
			this.data = data.data;
		}).catch(err => {})
	},
	methods: {
		appGetHotels(params) {
			appGetHotels(params).then(data => {
				if (data.errno == 200) {
					if (data.data.length === 0) {
						uni.showToast({
							icon: 'none',
							title: '我们也是有底线的'
						})
						return;
					}
					uni.showToast({
						icon: 'none',
						title: data.errmsg
					})
					data.data.forEach(item => {
						this.data.push(item)
					})
				} else {
					uni.showToast({
						icon: 'none',
						title: data.errmsg
					})
				}
			}).catch(err => {
				uni.showToast({
					icon: 'none',
					title: err.message
				})
			})
		},
		appGetMS(params) {
			appGetMS(params).then(data => {
				if (data.errno == 200) {
					if (data.data.length === 0) {
						uni.showToast({
							icon: 'none',
							title: '我们也是有底线的'
						})
						return;
					}
					uni.showToast({
						icon: 'none',
						title: data.errmsg
					})
					data.data.forEach(item => {
						this.data.push(item)
					})
				} else {
					uni.showToast({
						icon: 'none',
						title: data.errmsg
					})
				}
			}).catch(err => {
				uni.showToast({
					icon: 'none',
					title: err.message
				})
			})
		},
		goHome(item) {
			console.log(JSON.stringify(this.params))
			if (this.params.time) {
				uni.navigateTo({
					url: '../../homeModel/home?hotelId=' + item.id + '&date=' + this.params.time + '&countNight=' + this.countNight
				})
			} else {
				uni.navigateTo({
					url: '../../homeModel/home?hotelId=' + item.id
				})
			}

		},
		searchHotel() {
			// 同button的事件
			let params = {
				ids: this.inputVal
			}
			getHotelInfoByHotelName(params).then(data => {
				this.data = data.data;
			}).catch(err => {})
		}
	}
}
